exports.push([module.id, "\n.head {\n  height: 50px;\n  width: 50px;\n  border-radius: 50%;\n}\n", "", {"version":3,"sources":["/Users/fanxinqi/Desktop/洗衣帮项目/xyb-fe/xi-fe/src/fragment/category/src/fragment/category/edit.vue"],"names":[],"mappings":";AACA;EACA,aAAA;EACA,YAAA;EACA,mBAAA;CACA","file":"edit.vue","sourcesContent":["<style>\n.head {\n  height: 50px;\n  width: 50px;\n  border-radius: 50%;\n}\n</style>\n\n<template>\n    <Form ref=\"formValidate\" :model=\"formData\" :rules=\"ruleValidate\" :label-width=\"80\">\n        <FormItem label=\"分类名\" prop=\"name\">\n            <Input v-model=\"formData.name\" placeholder=\"请输入你的名字\"></Input>\n        </FormItem>\n        <FormItem label=\"图片\">\n            <img :src=\"formData.imageEntity.url\" class=\"head\">\n            <Upload\n                ref=\"upload\"\n                :show-upload-list=\"false\"\n                :on-success=\"handleSuccess\"\n                :format=\"['jpg','jpeg','png']\"\n                :max-size=\"2048\"\n                :on-format-error=\"handleFormatError\"\n                :on-exceeded-size=\"handleMaxSize\"\n                :before-upload=\"handleBeforeUpload\"\n                multiple\n                type=\"drag\"\n                action=\"/api/attachment/upload\"\n                style=\"display: inline-block;width:58px;\">\n                <div style=\"width: 58px;height:58px;line-height: 58px;\">\n                    <Icon type=\"camera\" size=\"20\"></Icon>\n                </div>\n            </Upload>\n        </FormItem>\n        <FormItem label=\"价格\" prop=\"price\">\n            <Input v-model=\"formData.price\" placeholder=\"请输入价格\"></Input>\n        </FormItem>\n        <FormItem label=\"描述\" prop=\"des\">\n            <Input v-model=\"formData.des\" type=\"textarea\" :autosize=\"{minRows: 2,maxRows: 5}\" placeholder=\"请输入相关描述\"></Input>\n        </FormItem>\n        <FormItem>\n            <Button type=\"primary\" @click=\"handleSubmit('formValidate')\">保存</Button>\n        </FormItem>\n    </Form>\n</template>\n<script>\nimport { util } from \"../../lib/tools\";\nexport default {\n  props: [\"formData\"],\n  data() {\n    return {\n      formValidate: this.formData || { name: \"\", img: \"\", price: \"\", des: \"\",parentId:0 },\n      ruleValidate: {\n        name: [{ required: true, message: \"分类名不能为空\", trigger: \"blur\" }],\n        price: [{ required: true, message: \"价格不能为空\", trigger: \"blur\" }]\n      }\n    };\n  },\n  methods: {\n    handleSubmit() {\n      this.$refs[\"formValidate\"].validate(valid => {\n        if (valid) {\n          let actionName = \"saveCategory\";\n          if (this.formData.id) {\n            actionName = \"updateCategory\";\n          }\n          let postData = util.clone(this.formData);\n          if(!postData.imageEntity.id){\n            delete postData.imageEntity;\n          }\n          this.$store.dispatch(\"proxyAction\", {\n              name: actionName,\n              queries: null,\n              data: postData,\n              param: null\n            })\n            .then(res => {\n              this.$store.dispatch(\"reload\", {\n                routeName: this.$route.name\n              });\n            });\n        } else {\n          this.$Message.error(\"验证失败!\");\n        }\n      });\n    },\n    handleReset(name) {\n      this.$refs[name].resetFields();\n    },\n    handleSuccess(res, file) {\n      this.formData.imageEntity.url = res.data.url;\n      this.formData.imageEntity.id=res.data.id;\n    },\n    handleFormatError(file) {\n      this.$Notice.warning({\n        title: \"The file format is incorrect\",\n        desc:\n          \"File format of \" +\n          file.name +\n          \" is incorrect, please select jpg or png.\"\n      });\n    },\n    handleMaxSize(file) {\n      this.$Notice.warning({\n        title: \"Exceeding file size limit\",\n        desc: \"File  \" + file.name + \" is too large, no more than 2M.\"\n      });\n    },\n    handleBeforeUpload() {}\n  }\n};\n</script>\n"],"sourceRoot":""}]);

