exports.push([module.id, "\n.head[data-v-1d4fcdce]{\n    width: 50px;\n    height: 50px;\n    border-radius: 50%;\n}\n\n", "", {"version":3,"sources":["/Users/fanxinqi/Desktop/洗衣帮项目/xyb-fe/xi-fe/src/fragment/store/src/fragment/store/edit.vue"],"names":[],"mappings":";AA4HA;IACA,YAAA;IACA,aAAA;IACA,mBAAA;CACA","file":"edit.vue","sourcesContent":["<template>\n    <Form ref=\"formData\" :model=\"formData\" :rules=\"ruleValidate\" :label-width=\"80\">\n        <FormItem label=\"店铺名称\" prop=\"name\">\n            <Input v-model=\"formData.name\" placeholder=\"请输入你的店铺名称\"></Input>\n        </FormItem>\n        <FormItem label=\"头像\">\n            <img :src=\"formData.imageEntity.url\" class=\"head\">\n            <Upload\n                    ref=\"upload\"\n                    :show-upload-list=\"false\"\n                    :on-success=\"handleSuccess\"\n                    :format=\"['jpg','jpeg','png']\"\n                    :max-size=\"2048\"\n                    :on-format-error=\"handleFormatError\"\n                    :on-exceeded-size=\"handleMaxSize\"\n                    :before-upload=\"handleBeforeUpload\"\n                    multiple\n                    type=\"drag\"\n                    action=\"/api/attachment/upload\"\n                    style=\"display: inline-block;width:58px;\">\n                <div style=\"width: 58px;height:58px;line-height: 58px;\">\n                    <Icon type=\"camera\" size=\"20\"></Icon>\n                </div>\n            </Upload>\n            <!-- <Modal title=\"View Image\" v-model=\"visible\">\n                <img :src=\"'https://o5wwk8baw.qnssl.com/' + imgName + '/large'\" v-if=\"visible\" style=\"width: 100%\">\n            </Modal> -->\n        </FormItem>\n\n\n        <FormItem label=\"地址\" prop=\"address\">\n            <Input v-model=\"formData.address\" type=\"textarea\" :autosize=\"{minRows: 2,maxRows: 5}\" placeholder=\"请输入店铺地址\"></Input>\n        </FormItem>\n        <FormItem label=\"描述\" prop=\"des\">\n